    Vue.use(Button);
    Vue.use(Cell).use(CellGroup);
    Vue.use(Image);
    Vue.use(Toast);
    Vue.use(Card);
    Vue.use(SubmitBar);
    Vue.use(Checkbox).use(CheckboxGroup);
    Vue.use(Tag);
